<template>
  <div class="korean">
    <div class="korean_nav_bg">
      <table class="korean_nav">
        <td class="nav_row" v-for="(item,key) in nav_title" v-bind:key="item.id">
          <h2 :class="{'korean_nav_item': true,
        'nav_active': key === nav_chose, 'left_radius': key===0, 'right_radius': key === nav_title.length-1}"
              v-on:click = "onNavSelected(key)" >{{item.name}}</h2>
        </td>
      </table>
    </div>

    <table class="table">
      <tr v-for="m in current.lines" class="lines">
        <td v-for="n in current.rows" :class="{'normal_row': nav_chose!==1,'more_row':nav_chose===1, 'korean_active': selected===(m-1)*current.rows+(n-1)}" v-on:click = "onItemSelected(m,n)">
          <img :src="'./static/korean/drawable/' + letters[(m-1)*current.rows+(n-1)]+'.png'">
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
  import { mapGetters } from 'vuex'
  export default {
    name: 'koreans',
    computed: mapGetters({
      accountState: 'getAccountState'
    }),
    data(){
      return{
        selected: -1,
        letters: [],
        current: {lines:20, rows:11},
        nav_chose: 0,
        nav_title: [{name:'ㅏ~ㅣ'}, {name:'ㅐ~ㅓ'}],
      }
    },
    methods: {
      onNavSelected (key) {
        this.nav_chose = key
        this.current = key===0? {lines:20, rows:11}: {lines:20, rows:12}
        this.initShow(key)
      },
      onItemSelected(m, n){
        this.selected = (m-1)*this.current.rows+(n-1)
        var mp3 = "./static/korean/raw/"+"korean"+this.selected+(this.nav_chose===1?"two":"")+".mp3"
        console.log(mp3)
        this.gamemuiscs1.src = mp3
        this.gamemuiscs1.play()
      },
      initShow(pageNum){
        var array = [];
        var lines = this.current.lines
        var rows = this.current.rows
        for(var i =0; i< lines; i++){
          for(var j= 0; j < rows; j++){
            var nomal = 'korean'+(i*rows+j)
            if(pageNum===1){
              nomal = nomal+'_two'
            }
            array.push(nomal)
          }
        }
        this.letters = array;
      }
    },

    created(){
      if(!this.accountState.today){
        alert('Please sign first!')
        this.$router.push('/')
        return
      }
      this.initShow(0)
      this.gamemuiscs1 = new Audio()
    }
  }
</script>

<style>
  .korean_nav_bg{
    width: 100%;
    margin-right: 0em;
    background: whitesmoke;
    box-shadow: 0.1em 0.3em 0.3em #072927;
    text-align: center;
    margin-bottom: 1%;
  }

  .korean_nav {
    width: 40%;
    height: 1em;
    margin-left: 30%;
    text-align: center;
    /*border-radius: 1em 1em 1em 1em;*/
    /*box-shadow: 0.1em 0.1em 0.1em 0.1em #FF7CA4;*/
    /*background: linear-gradient(#f0ba02, #cd7015, #f0ba02);*/
  }
  .korean_nav_item{
    text-align: center;
    margin-top: 0.5em;
    padding: 5px;
    font-size: 1em;
    background: white;
    font-weight:bold;
    border-color: #FF7CA4;
  }
  .korean .left_radius{
    border-radius: 1em 0em 0em 1em;
  }

 .korean .right_radius{
    border-radius: 0em 1em 1em 0em;
  }

 .korean .nav_active{
    background: #FF7CA4;
    border-color: #FF7CA4;
    color:white;
    font-weight:bold;
  }

  table{
    border-collapse:collapse;
    background-color: whitesmoke;
  }
  .table tr {
    text-align: center;
    margin: 0em;
    padding: 0em;
  }
  .table img{
    padding: 0em;
    margin: 0em;
    /*background: gainsboro;*/
  }

 .korean .normal_row{
    text-align: center;
    width:2%;
  }

 .korean .more_row{
    width:1%;
    text-align: center;
  }

  .korean_active{
    border-radius: 1em 1em 1em 1em;
    border-color: #FF7CA4;
    box-shadow: 0.1em 0.1em 0.1em 0.1em #FF7CA4;
  }

</style>



