import Vue from 'vue'
import Router from 'vue-router'
import japanese from '@/components/Japanese'
import home from '@/components/Home'
import koreans from '@/components/Koreans'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/japanese',
      name: 'japanese',
      component: japanese
    },
    {
      path: '/koreans',
      name: 'koreans',
      component: koreans
    }
  ]
})



